*{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  body{
    margin: 0;
  }

  .navgrid{
    position: absolute;
      width: 100%;
      display: flex;
      justify-content: center;
  }

.navgrid ul{
  display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 11rem;
    width: fit-content;

    align-items: center;
}

.navgrid li{
  list-style-type: none;
}
.navgrid li  {
  list-style-type: none;
  text-decoration: none;
}
.navgrid a {
  text-decoration: none;}
.navgrid :visited{
  color:#b1bebe99;;
}
.navgrid a:hover{
  color: black;
}




.img{
  width: 100%;
}
@keyframes appear {
  0% {
    opacity: 0;
    filter: blur(.1rem);
    
  }

  50% {
    opacity: 0;

    filter: blur(0.2rem);
    
  }
     70% {
       opacity: 0;
 
       filter: blur(0.2rem);
  
     }

            90% {
              opacity: 0;
      
            }

  100% {
    opacity: 1;
    filter: blur(0rem);

  }
}
