#hello,#side{
  font-family: 'Raleway','serif';
}
#hello {
margin-bottom: 0;
    font-weight: 500;
  display: inline-block;
  font-size: 5rem;
  animation: sideAppear 2.4s ease forwards;

}

#side {
      position: relative;
        top: -0.6rem;
  opacity: 0;
    display: inline-block;
        margin-left: 2rem;
          font-size: 3rem;
          margin-bottom: 0;
    margin-top: 1.9rem;
    animation: sideAppear 3s ease forwards;
    font-weight: 500;
}

@keyframes helloMove {
  0% {
    transform: translateX(10rem);
    opacity: 1;
  }

  50% {
    transform: translateX(0);
    opacity: 0;
  }

  100% {
    transform: translateX(0%);
    opacity: 0;
  }
}

@keyframes sideAppear {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }

  50% {
    opacity: 0;
    transform: translateX(0);
  }

  85% {
    opacity: 1;
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

